@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;1,300;1,400;1,500&display=swap');
// Colors
$bg : hsl(228, 33%, 97%);
$heading : hsl(238, 40%, 52%);
*{
  box-sizing: border-box;
}
body{
  font-family: 'Roboto', 'times', sans-serif;
  background-color: $bg;
}
header{
  text-align: center;
  font-family: 'Pacifico', 'cursive';
  color:$heading;
  background-color: hsl(239, 57%, 85%);
  padding: 1rem 0;
  h1{
    margin:0;
  }
}
main{
  display:flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  max-width: 90ch;
  margin: 0 auto;
}
ul.posts{
  margin:0;
  padding:0;
  li{
    list-style-type: none;
    padding: 1rem;
    background-color: #e7e2f9;
    border-radius: 10px;
    margin-bottom: 1rem;
    min-width: 80ch;
    p{
      margin:0;
      span.userName{
        margin-right: 1rem;
        font-size: 1.25rem;
      }
      span.date{
        margin-right: 1rem;
      }
      span.like{
        cursor: pointer;
        &:hover{
          font-size: 1.1rem;
        }
      }
    }
    span.reply{
      font-weight: bolder;
      color:$heading;
      font-style: italic;
      cursor: pointer;
    }
    div.nameDate{
      display:flex;
      justify-content: space-between;
    }
    p.post{
      border-bottom: 1px solid grey;
      margin: 1rem 0;
      margin-bottom: 0;
      padding-bottom: .5rem;
      color:#5e5d5d;
      line-height: 1.5rem;
    }
    ul.commentItems{
      margin:0;
      padding:0;
      padding-top:.5rem;
      li{
        padding: .25rem .5rem;
        padding-left:1rem;
        margin:.25rem 0;
        border-radius: 5px;
        background-color: #dbcefa;
        font-size: .85rem;
        font-style: italic;
      }
    }
    form.replySubmit{
      display:flex;
      justify-content: space-between;
      margin-top: 1rem;
      input[type=text]{
        width:90%;
      }
      input[type=submit]{
        color:#fff;
        background-color: $heading;
        padding:.25rem .5rem;
        border-radius: 5px;
        border: none;
      }
    }
  }
}
h3{
  text-align: center;
  color:$heading;
}
form.postSubmit{
  display:flex;
  justify-content: space-between;
  padding: .5rem;
  border-radius: 10px;
  background-color: #d7cef7;
  input[type=text], textarea{
    text-align: center;
    height:2rem;
    font-family: 'Roboto', 'times', sans-serif;
    align-content: center;
  }
  input[type=submit]{
    background-color: $heading;
    padding: .5rem 1rem;
    color:#fff;
    font-weight: bolder;
    border-radius: 5px;
    border:none;
  }
}
@media screen and (max-width: 780px){
  header{
    padding:.5rem;
    h1{
      font-size: 1.5rem;
    }
  }
  main{
    padding: 1rem;
  }
  ul.posts{
    margin:0;
    max-width: 100vw;
    li{
      min-width: auto;
      margin:1rem;
    }
  }
  ul.commentItems{
    li{
      min-width: auto;
    }
  }
  form.postSubmit{
    margin:1rem;
    input[type=text], textarea, input[type=submit]{
      margin: 0 .5rem;
      max-height: 1.25rem;
    }
    input[type=submit]{
      padding: 0 .5rem;
    }
  }
}