@import url("https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;1,300;1,400;1,500&display=swap");
* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", "times", sans-serif;
  background-color: hsl(228, 33%, 97%);
}

header {
  text-align: center;
  font-family: "Pacifico", "cursive";
  color: hsl(238, 40%, 52%);
  background-color: hsl(239, 57%, 85%);
  padding: 1rem 0;
}
header h1 {
  margin: 0;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  max-width: 90ch;
  margin: 0 auto;
}

ul.posts {
  margin: 0;
  padding: 0;
}
ul.posts li {
  list-style-type: none;
  padding: 1rem;
  background-color: #e7e2f9;
  border-radius: 10px;
  margin-bottom: 1rem;
  min-width: 80ch;
}
ul.posts li p {
  margin: 0;
}
ul.posts li p span.userName {
  margin-right: 1rem;
  font-size: 1.25rem;
}
ul.posts li p span.date {
  margin-right: 1rem;
}
ul.posts li p span.like {
  cursor: pointer;
}
ul.posts li p span.like:hover {
  font-size: 1.1rem;
}
ul.posts li span.reply {
  font-weight: bolder;
  color: hsl(238, 40%, 52%);
  font-style: italic;
  cursor: pointer;
}
ul.posts li div.nameDate {
  display: flex;
  justify-content: space-between;
}
ul.posts li p.post {
  border-bottom: 1px solid grey;
  margin: 1rem 0;
  margin-bottom: 0;
  padding-bottom: 0.5rem;
  color: #5e5d5d;
  line-height: 1.5rem;
}
ul.posts li ul.commentItems {
  margin: 0;
  padding: 0;
  padding-top: 0.5rem;
}
ul.posts li ul.commentItems li {
  padding: 0.25rem 0.5rem;
  padding-left: 1rem;
  margin: 0.25rem 0;
  border-radius: 5px;
  background-color: #dbcefa;
  font-size: 0.85rem;
  font-style: italic;
}
ul.posts li form.replySubmit {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}
ul.posts li form.replySubmit input[type=text] {
  width: 90%;
}
ul.posts li form.replySubmit input[type=submit] {
  color: #fff;
  background-color: hsl(238, 40%, 52%);
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
  border: none;
}

h3 {
  text-align: center;
  color: hsl(238, 40%, 52%);
}

form.postSubmit {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  border-radius: 10px;
  background-color: #d7cef7;
}
form.postSubmit input[type=text], form.postSubmit textarea {
  text-align: center;
  height: 2rem;
  font-family: "Roboto", "times", sans-serif;
  align-content: center;
}
form.postSubmit input[type=submit] {
  background-color: hsl(238, 40%, 52%);
  padding: 0.5rem 1rem;
  color: #fff;
  font-weight: bolder;
  border-radius: 5px;
  border: none;
}

@media screen and (max-width: 780px) {
  header {
    padding: 0.5rem;
  }
  header h1 {
    font-size: 1.5rem;
  }
  main {
    padding: 1rem;
  }
  ul.posts {
    margin: 0;
    max-width: 100vw;
  }
  ul.posts li {
    min-width: auto;
    margin: 1rem;
  }
  ul.commentItems li {
    min-width: auto;
  }
  form.postSubmit {
    margin: 1rem;
  }
  form.postSubmit input[type=text], form.postSubmit textarea, form.postSubmit input[type=submit] {
    margin: 0 0.5rem;
    max-height: 1.25rem;
  }
  form.postSubmit input[type=submit] {
    padding: 0 0.5rem;
  }
}/*# sourceMappingURL=App.css.map */